@charset 'utf-8';

:root {
  font-size: 15px;
  color: #202020;
  font-family: 'DM Sans', sans-serif;
}

.inner {
  max-width: 1200px;
  margin: 0 auto;
}

.bg {
  height: 500px;
  border: 1px solid #ccc;
  background-color: #ccc;
  font-weight: 600;
  padding: 20px;
}

.bg b {
  color: red;
}

img {
  width: 100%;

}

button,
input {
  border: none;
  outline: none;
  background: transparent;
}


.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #ccc;
  height: 100px;
  padding: 0 40px;
}

.Header .logo {
  width: 120px;
}

.Header div {
  display: flex;
}

.Header .gnb {
  display: flex;
}

.Header .gnb a {
  padding: 0.2rem;
  font-weight: bold;
}

.Header .icon {}

.Header .icon a {
  padding: 0 0.5rem;
}

.Section {
  border: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
}

.Main {
  height: 500px;
  width: 500px;
  background-color: lightblue;
  margin: 30px;
  border-radius: 30px;
  position: relative;
}

.Main div {
  position: absolute;
  width: 80%;
  left: 50%;
  top: 70%;
  transform: translate(-50%, -50%);
  text-align: left;
}

.Main h2 {
  color: #Fff;
  font-size: 30px;
  font-weight: 700px;
}

.Main p {
  margin-top: 20px;
  color: rgb(117, 117, 117);
}

.Tabs {
  border: 1px solid #000;
  width: 300px;
  height: 300px;
}

.Data {
  border: 1px solid #ccc;
  width: 300px;
  height: 200px;
  position: relative;
  border-radius: 5px;
}

.Data div {
  position: absolute;
  width: 80%;
  left: 50%;
  top: 70%;
  transform: translate(-50%, -50%);
  text-align: left;
}

.Data h2 {
  color: #000;
  font-size: 30px;
  font-weight: 700px;
}

.Data p {
  margin-top: 20px;
  color: rgb(117, 117, 117);
}

/* Tabs.css */

.Tabs {
  max-width: 600px;
  margin: 0 auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
}

.TabList {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  border-bottom: 1px solid #ccc;
}

.TabList li {
  padding: 10px 20px;
  cursor: pointer;
  border-bottom: 2px solid transparent;
}

.TabList li.active {
  border-bottom-color: #007bff;
  color: #007bff;
}

.TabContent div {
  display: none;
  padding: 20px;
}

.TabContent div.active {
  display: block;
}

/* App.css */

/* 활성화된 링크에 대한 스타일 */
.nav-link.active {
  background-color: #007bff;
  color: #fff;
}